// 
export const setCurrentGoods = good => ({
  type: 'SET_CURRENT_GOOD',
  good,
});

// 
export const addToCart = good => ({
  type: 'ADD_TO_CART',
  good,
});

// 
export const decreaseFromCart = good => ({
  type: 'DECREASE_FROM_CART',
  good,
});



